.search{height:140px;border-radius:8px;background-image:url(/brews-app/static/media/backgroundImg.88955ec1.jpg);background-position:center;background-size:cover;display:-webkit-flex;display:flex}.search__input{min-width:200px;width:65%;height:50px;border-radius:4px;background:white;margin:auto;display:-webkit-flex;display:flex;padding:3px}.search__input form{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.search__input form *{font-family:'Roboto', sans-serif}.search__input input{width:80%;padding:0 7px;border:none;color:#334680;font-size:0.9rem}.search__input input:focus{outline:none}.search__input input::-webkit-input-placeholder{color:#B9BDCF}.search__input input:-ms-input-placeholder{color:#B9BDCF}.search__input input::placeholder{color:#B9BDCF}.search__input button{height:100%;width:120px;background:#1E86FF;border:none;border-radius:4px;color:white;font-size:1.2rem;cursor:pointer}.search__icon{color:#B9BDCF;position:relative;top:-11px;margin-right:5px;margin-left:5px}.search__icon--text{color:#334680}@media (max-width: 800px){.search__input{width:95%}}

