.pages{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.pages__arrow,.pages__number,.pages__more{width:32px;height:32px;border:1px solid;border-radius:4px;margin:5px}.pages__arrow p,.pages__number p,.pages__more p{margin:auto 0}.pages__arrow:hover,.pages__number:hover{color:#1E86FF;border-color:#1E86FF}.pages__arrow--disabled{pointer-events:none;width:32px;height:32px;border:1px solid;border-radius:4px;margin:5px;color:#d7dae4;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.pages__arrow--disabled p{margin:auto 0}.pages__number,.pages__arrow{color:#B9BDCF;border-color:#B9BDCF;font-family:'Roboto', sans-serif;cursor:pointer;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.pages__number--current,.pages__arrow--current{border-color:#1E86FF;background:#1E86FF;color:white;pointer-events:none}.pages__more{position:relative;top:-11px;pointer-events:none;font-weight:700;font-size:2rem;border-color:rgba(0,0,0,0);color:#B9BDCF}

